@font-face{
  src:url(./fonts/Comfortaa-VariableFont_wght.ttf);
  font-family: Comfortaa;
}

.flex-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
}

html,body, #root {
  height: 100%;
  background-color: rgb(63, 67, 76);
  font-family: 'Comfortaa','Lucida Console','Courier New', Courier, monospace;
  color:#c2c5cc;
  background: linear-gradient( rgba(0, 0, 0, .2), rgb(0, 0, 0,.2) ),url("./img/Goclenius1920.png");
  background-position: center;
  background-size: cover;
}

.footer-container{
  width: 100%;
}

.footer {
  background-color: rgb(63, 67, 76);
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Navigation */
nav {
  background: linear-gradient( rgba(0, 0, 0, 1), rgb(0, 0, 0,0) );
  font-size: 1.5rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



a:link {
  color: #78d1fa;
}

/* visited link */
a:visited {
  color: rgb(170, 14, 209);
}

/* mouse over link */
a:hover {
  color: rgb(238, 15, 219);
}

/* selected link */
a:active {
  color: blue;
}


.page-container {
  background: linear-gradient( rgba(12, 12, 12, 0.9), rgba(12, 12, 12, 0.9) );
}