    top: 40%;
    width: 100%;
    text-align: center;
  }

.caption {    
    left: 0;
    top: 40%;
    width: 100%;
    text-align: center;
}

/* Paralaxing background */
.home-banner-img  {
    position: relative;
    opacity: 0.9;
    background-attachment: fixed;
    
    background-position: bottom 50px right 0px;
    background-repeat: no-repeat;
    background-size: cover;
  
  }
  .home-banner-img {
    background-image: url("../img/home/sccmeajqeze.png");
    min-height: 100%;
    height: 300px;
    width: 100%;
  }

  /* Turn off parallax scrolling for tablets and phones */
  @media only screen and (max-device-width: 1024px) {
    .home-banner-img
    {
      background-attachment: scroll;
    }
  }
  